import zTable from '@/components/z-table/z-table.vue';
export default {
	components: {
		zTable
	},
	data() {
		return {
			vehicles: [],
			id: '',
			plateNo: '',
			columns: [
				{
					title: '序号',
					key: 'index',
				},{
					title: '车牌号',
					key: 'plateNo',
				},
				{
					title: '强制报废日期',
					key: 'forceRetireDate',
				},
				{
					title: '检验有效期至',
					key: 'checkValidDate',
				},	
				{
					title: '上次验车时间',
					key: 'lastCheckDate',
				}
			]
		};
	},
	onLoad(option = {}) {
		this.id = JSON.parse(option.vehicles).id;
		this.fetch();
	},
	methods: {
		onSearch(event) {
			this.fetch({plateNo:event.detail})
		},
		tableSelect(val) {
			console.log(val);
		},
		async fetch(params = {}) {
			const { id, $http } = this;
			const res = await $http.get(`api/info/company/${id}/vehicle`, { page: 0, size: 9999, ...params });
			this.vehicles = res.content;
		}
	}
};
